<script setup>
import { ref } from 'vue';

import Footer from '@/components/core/Footer.vue';

const catGeneral = ref();
const catRooms = ref();
// const catCampsites = ref();
const catBooking = ref();
// const catTrails = ref();

const category = ref('general');

function change(_category) {
  if (category.value !== _category) {
    const prev = category.value;
    category.value = _category;
    reset(prev);
  }
}

function toggle(question) {
  if (category.value === 'general') {
    catGeneral.value.querySelector(`#${question}`).classList.toggle('maximized');
  } else if (category.value === 'rooms') {
    catRooms.value.querySelector(`#${question}`).classList.toggle('maximized');
    // } else if (category.value === 'campsites') {
    //   catCampsites.value.querySelector(`#${question}`).classList.toggle('maximized');
  } else if (category.value === 'booking') {
    catBooking.value.querySelector(`#${question}`).classList.toggle('maximized');
    // } else if (category.value === 'trails') {
    //   catTrails.value.querySelector(`#${question}`).classList.toggle('maximized');
  }
}

function reset(_category) {
  if (_category === 'general') {
    for (let i = 0; i < catGeneral.value.children.length; i++) {
      catGeneral.value.children[i].classList.remove('maximized');
    }
  } else if (_category === 'rooms') {
    for (let i = 0; i < catRooms.value.children.length; i++) {
      catRooms.value.children[i].classList.remove('maximized');
    }
  } else if (_category === 'booking') {
    for (let i = 0; i < catBooking.value.children.length; i++) {
      catBooking.value.children[i].classList.remove('maximized');
    }
  }
}
</script>

